td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
  }
  
  table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 80%;
  }

  tr:nth-child(even) {
    background-color: #dddddd;
  }
  .tableTitle {
      text-align: center;
  }
  